.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #408F95;
  padding: 1rem 2.8125rem 1rem 2.5rem;
  position: relative;
  z-index: 1;
  text-align: center;
}

.button span {
  display: block;
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  line-height: 1.375rem;
  font-size: 0.875rem;
  padding-right: 2.75rem;
  position: relative;
}

.button span::before {
  content: "";
  width: 2.5rem;
  height: 0.375rem;
  border-right: 2px solid #fff;
  border-bottom: 1px solid #fff;
  transform: skew(45deg);
  display: block;
  position: absolute;
  top: 0.375rem;
  right: 0;
}

.button::before {
  content: "";
  width: 100%;
  height: 100%;
  border: 1px solid #408F95;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  z-index: -2;
}

.button::after {
  background: #fff;
  border: 1px solid #408F95;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover span {
  color: #408F95;
}

.button:hover span::before {
  border-color: #408F95;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button.button--white {
  background-color: #fff;
}

.button.button--white span {
  color: #408F95;
}

.button.button--white span::before {
  border-right: 2px solid #408F95;
  border-bottom: 1px solid #408F95;
}

.button.button--white::before {
  border: 1px solid #fff;
}

.button.button--white::after {
  display: none;
}

.button.button--white:hover {
  opacity: 0.8;
}

.button.button--white:hover span {
  color: inherit;
}

.button.button--white:hover span::before {
  border-color: inherit;
}

