/*チェックボックスの指定*/
/* テキストエリアの指定 */
/*----- 共通エリアの指定 -----*/
[type=text],
[type=search],
[type=tel],
[type=url],
[type=email],
[type=tel],
[type=password],
[type=date],
textarea {
  /*↓リセットCSS */
