  transition: border 0.3s;
  /*↓↓↓↓不要ならコメントアウト or 削除 */
  /*↓↓選択されているときの色*/
}
@media screen and (min-width: 768px) {
  [type=text],
  [type=search],
  [type=tel],
  [type=url],
  [type=email],
  [type=tel],
  [type=password],
  [type=date],
  textarea {
    font-size: 1rem;
  }
}

input::placeholder {
  color: #DDF0F1;
}

/*----- 電話のスタイル ------*/
[type=tel] {
  width: 100%;
}

/*----- ラジオボタンのスタイル ------*/
[type=radio] {
  opacity: 0;
  position: absolute;
}
[type=radio] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.375rem;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  [type=radio] + span {
    font-size: 1rem;
  }
}
[type=radio] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border: 0.0625rem solid #408F95;
  border-radius: 50%;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
  transition: border 0.3s;
}
[type=radio] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=radio]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0.1875rem;
  transform: translateY(-50%);
  background: #408F95;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  display: block;
  opacity: 1;
}

/*----- チェックボックスのスタイル ------*/
[type=checkbox] {
  width: 1px;
  height: 1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  line-height: 22px;
}
[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.5rem;
  margin: 0;
  font-size: 0.875rem;
  line-height: 1.5;
  cursor: pointer;
  text-align: left;
  letter-spacing: 0.14em;
}
@media screen and (min-width: 768px) {
  [type=checkbox] + span {
    font-size: 1rem;
    letter-spacing: 0;
  }
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 0.1875rem;
  left: -0.0625rem;
  background: #fff;
  border: 1px solid #408F95;
  width: 1rem;
  height: 1rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 0.4375rem;
  left: 0.125rem;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: rotate(-45deg);
  width: 9px;
  height: 5px;
  border-left: 2px solid #C94800;
  border-bottom: 2px solid #C94800;
  /*backgroundでチェックマークを再現*/
}

/*----- セレクトボックスのスタイル ------*/
select {
  /*↓リセットCSS */
  border-radius: 0;
  font-family: inherit;
  color: inherit;
  cursor: pointer;
  width: 100%;
  /*↓カスタム */
  font-size: 0.75rem;
  padding: 0.6875rem 0.5rem;
  background-color: #fff;
  border: 0.0625rem solid #408F95;
  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  appearance: none;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
  background: url(../images/common/polygon.svg) no-repeat center right 9px/12px 10px;
}
@media screen and (min-width: 768px) {
  select {
    font-size: 1rem;
  }
}

/*----- 日付のスタイル ------*/
[type=date] {
  position: relative;
  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  appearance: auto;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
}

input[type=date]::-webkit-calendar-picker-indicator {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}

/*----- テキストエリアのスタイル ------*/
textarea {
  height: 12.625rem;
  width: 100%;
  transition: border 0.3s;
  resize: none;
  border: 0.0625rem solid #408F95;
}
@media screen and (min-width: 768px) {
  textarea {
    font-size: 1rem;
  }
}

/*----- 送信ボタン ------*/
[type=button],
[type=submit] {
  /*↓リセットCSS */
  margin: 0;
  background-image: none;
  width: auto;
  display: inline-block;
  text-decoration: none;
  border: none;
  border-radius: 0;
  /*↓カスタム */
  color: #fff;
}

/* ボタンをホバーしたときのスタイル */
button:hover [type=button],
button:hover [type=submit] {
  color: #408F95;
}

span.wpcf7-spinner {
  display: none;
}

.wpcf7 form.invalid .wpcf7-response-output {
  border: none;
  text-align: center;
  color: #C94800;
